/******** base color css ********/
.btn_1 {
    background-color: $base_color;
    border: 1px solid $base_color;
    color: $white;
    display: inline-block;
    padding: 11px 23px;
    text-transform: none;
    line-height: 16px;
    font-size: 13px;
    font-weight: 500;
    border-radius: 5px;
    white-space: nowrap;
    @include transform_time(.5s);

    i {
        font-size: 15px;
        padding-right: 7px;
    }

    &:hover {
        background-color: $white;
        border: 1px solid $base_color;
        color: $base_color;
    }

    &.light {
        background-color: rgba($color: $base_color, $alpha: .1);
        border: 1px solid transparent;
        color: $base_color;
    }

    &.green {
        background-color: rgba($color: #2EC9B8, $alpha: .1);
        border: 1px solid transparent;
        color: #2EC9B8;
    }

    &.sm {
        font-size: 12px;
        padding: 6px 15px;
    }

    &.big_btn {
        padding: 11px 36px;
    }

    &.form_big_btn_1 {
        padding: 16px 62px !important;

        @media #{$small_mobile} {
            padding: 16px 30px !important;
        }
    }

    &.big_btn2 {
        padding: 11px 37px;
    }
}

.btn_2 {
    color: $base_color;
    border: 1px solid $base_color;
    display: inline-block;
    padding: 11px 23px;
    text-transform: none;
    line-height: 16px;
    font-size: 13px;
