.primary_btn_large {
    display: inline-block;
    padding: 17px 23px;
    text-transform: uppercase;
    line-height: 16px;
    font-size: 12px;
    font-weight: 500;
    border-radius: 5px;
    white-space: nowrap;
    @include transform_time(.3s);
    @extend %gradient_1;
    color: $white;
    border: 0;
    cursor: pointer;
    letter-spacing: 0.1em;

    i {
        font-size: 12px;
        padding-right: 6px;
    }

    &:hover {
        @extend %gradient_1;
        color: $white;
        box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25);
    }
}

