table.dataTable.dtr-inline.collapsed>tbody>tr[role="row"]>td:first-child::before,
table.dataTable.dtr-inline.collapsed>tbody>tr[role="row"]>th:first-child::before {
    // background-color:#222;
    z-index: 9;
    top: 50%;
    transform: translateY(-50%);
    content: "\e61a";
    font-family: 'themify';
    font-size: 8px;
    line-height: 14px;
    font-weight: 600;
    background-color: #fef1f2;
    box-shadow: 6px 6px 12px rgba(0, 0, 0, .08),
        -6px -6px 12px rgba(255, 255, 255, 1);

}

table.dataTable.dtr-inline.collapsed>tbody>tr.parent>td:first-child::before,
table.dataTable.dtr-inline.collapsed>tbody>tr.parent>th:first-child::before {

    content: "\e622";
    // background: #fe1724;
    box-shadow: inset 6px 6px 12px rgba(0, 0, 0, .08),
        inset -6px -6px 12px rgba(255, 255, 255, 1);

}



// CRM_dropdown 
.CRM_dropdown.dropdown {
    .dropdown-toggle {
        background: transparent;
        color: #415094;
        font-size: 12px;
        font-weight: 500;
        border: 1px solid #7c32ff;
        border-radius: 32px;
        padding: 2px 40px 5px 23px;
        text-transform: uppercase;
        overflow: hidden;
        transition: .3s;
        height: 32px;

        @media #{'only screen and (min-width: 992px) and (max-width: 1400px)'} {
            padding: 2px 30px 5px 23px;
        }

        &:after {
            content: "\e62a";
            font-family: "themify";
            border: none;
            border-top-color: currentcolor;
            border-top-style: none;
            border-top-width: medium;
            border-top: 0px;
            font-size: 12px;
            position: relative;
            top: 3px;
            left: 0;
            font-weight: 400;
            transition: .3s;
        }

        &:hover,
        &:focus {
            color: #fff;
            @extend %gradient_1;
            border: 1px solid transparent;
            box-shadow: none !important;
        }
    }

    &.show {
        .dropdown-toggle {

            &::after {}
        }
    }

    .dropdown-menu {
        border-radius: 5px 5px 10px 10px;
        border: 0px;
        padding: 15px 0px;
        box-shadow: var(--box_shadow);
        margin-top: 5px;

        &.full_width {
            min-width: 110px;
        }

        .dropdown-item {
            color: #828bb2;
            text-align: right;
            font-size: 12px;
            padding: 4px 1.5rem;
            text-transform: uppercase;
            cursor: pointer;

            &:hover {
                color: $base_color;
            }

            .active,
            &:active {
                color: $base_color;
                background-color: #f8f9fa;
            }
        }
    }

    &.CRM_dropdown_default {
        button {
            padding: 0 !important;
            border: 0 !important;
            text-transform: none;
            font-size: 13px;
            text-align: left;
            overflow: visible;

            &:hover {
                background: transparent;
                color: #415094;
            }

            &:hover,
            &:focus {
                border: 0;
                box-shadow: none !important;
                background: transparent;
                color: #415094;
            }

            &:after {
                content: "\f0d7";
                font-family: "Font Awesome 5 free";
                border: none;
                border-top-color: currentcolor;
                border-top-style: none;
                border-top-width: medium;
                border-top: 0px;
                font-size: 12px;
                position: relative;
                top: 3px;
                left: 0;
                font-weight: 900;
                transition: .3s;
                position: absolute;
                right: 0;
                left: auto;
                top: 4px;
            }
        }

        .dropdown-item {
            text-transform: capitalize !important;
            text-align: left;
        }
    }
}

.dropdown-menu.option_width_8 {
    min-width: 150px;
}

