.placeholder_input {
    background: #ECEEF4;
    height: 35px;
    border-radius: 30px;
    border: 0;
    width: 100%;
    padding: 0 15px;
    font-size: 13px;
    color: #000;

    &::placeholder {
        font-weight: 300;
        opacity: 1;
        font-size: 13px;
        color: #415094;
    }
}

