.placeholder_invoice_textarea {
    background: #ECEEF4;
    border-radius: 20px;
    border: 0;
    width: 100%;
    padding: 0 15px;
    font-size: 13px;
    color: #000;
    padding: 10px 15px;
    min-height: 35px;
    resize: block;

    &::placeholder {
        font-weight: 400;
        opacity: 1;
        font-size: 14px;
        color: #415094;
        line-height: 24px;
    }
}

.items_min_icon {
    i {
        color: #FF0000;
        font-size: 24px;
        cursor: pointer;
    }
}

.scrollable_tablist {
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
    overflow: auto;
    margin-bottom: 20px;
    margin-right: 45px;
    margin-left: 45px;

    @media #{'(min-width: 1635px)'} {
        margin-left: 0;
        margin-right: 0;
    }

    @media #{$small_mobile} {
        margin-left: 40px;
        margin-right: 40px;
    }

    li {
        margin-bottom: 0 !important;
    }
}

.scroll-left-button {
    left: 0;
    top: 0;
}

.scroll-right-button {
    right: 0;
    top: 0;
}

.scroll-left-button,
.scroll-right-button {
    width: 32px;
    height: 32px;
    text-align: center;
    line-height: 32px;
    cursor: pointer;
    display: inline-block;
    border-radius: 50%;
    padding: 0 !important;
    position: absolute;

    @media #{'(min-width: 1635px)'} {
        display: none;
    }

    i {
        line-height: 32px;
        margin: 0;
    }
}

