.task_expand_wrapper {
    padding: 25px;
    background: #FFFFFF;
    box-shadow: 0px 8px 10px rgba(236, 208, 244, 0.25);
    border-radius: 5px;
    width: 273px;
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    visibility: hidden;
    z-index: 9;
}

.task_expand_wrapper_open {
    .task_expand_wrapper {
        opacity: 1;
        visibility: visible;
    }
}

