.pop_up_form_hader {
    background: #FFFFFF;
    box-shadow: 0px 10px 15px rgba(235, 215, 241, 0.22);
    border-radius: 25px;
    padding: 17px 30px;
    cursor: pointer;
    position: relative;
    transition: .3s;

    &::before {
        position: absolute;
        content: "\e61a";
        font-family: 'themify';
        font-size: 14px;
        color: #415094;
        right: 30px;
        top: 50%;
        transform: translateY(-50%);
        font-weight: 400;
    }

    &.Company_Info_opened {
        box-shadow: 0px 10px 15px rgba(235, 215, 241, 0.22);
        border-radius: 10px 10px 0px 0px;

        &::before {
            content: "\e622";
        }
    }

    h3 {
        font-size: 14px;
        font-weight: 500;
    }
}

.company_form_popup {
    position: absolute;
    top: 20px;
    z-index: 9;
    width: 740px;
    background: #fff;
    padding: 30px 30px 40px 30px;
    box-shadow: 0px 10px 15px rgba(235, 215, 241, 0.22);
    border-radius: 0px 10px 10px 10px;
    opacity: 0;
    left: 0;
    transition: .3s;
    visibility: hidden;

    @media #{$tablet_device} {
        width: 670px;
    }

    @media #{$small_mobile} {
        width: 100%;
    }

    @media #{$large_mobile} {
        width: 100%;
    }

    &.Company_Info_active {
        opacity: 1;
        top: 50px;
        visibility: visible;
    }
}

.white-box.single-summery h3,
.white-box.single-summery p,
.white-box.single-summery h1 {
    position: relative;
    z-index: 1;
}