.primary_checkbox {
  display: inline-block;
  position: relative;
  width: 18px;
  height: 18px;
  margin: 0;
  flex: 18px 0 0;
  line-height: 18px;

  &.white_checkbox {
    .checkmark:after {
      border-color: $white;
    }
  }

  .checkmark {
    position: relative;
    width: 18px;
    height: 18px;
    top: 0;
    left: 0;
    display: block;
    cursor: pointer;
    line-height: 18px;
    flex: 18px 0 0;
    border-radius: 50%;
  }

  // .checkmark:after {
  //     content: "";
  //     position: absolute;
  //     display: none;
  // }
  input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
  }

  input:checked~.checkmark:after {
    // background: linear-gradient(90deg, #7C32FF 0.47%, #C738D8 100%);
    width: 100%;
    height: 100%;
    border: 0;
    // box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25);
    transition: .3s;
    transform: scale(0);
  }

  input:checked~.checkmark {
    background: linear-gradient(90deg, #7C32FF 0.47%, #C738D8 100%);
    // width: 100%;
    // height: 100%;
    // border: 0;
    box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25);
    transition: .3s;
  }

  input:checked~.checkmark::before {
    content: "\e64c";
    font-family: "themify";
    position: absolute;
    display: block;
    top: 0px;
    left: 3px;
    text-indent: 1px;
    color: #828BB2;
    background-color: transparent;
    border: 0px;
    -webkit-transform: rotate(8deg);
    -moz-transform: rotate(8deg);
    -ms-transform: rotate(8deg);
    -o-transform: rotate(8deg);
    transform: rotate(8deg);
    font-size: 10px;
    font-weight: 600;
    line-height: 18px;
    z-index: 99;
    color: #fff;
    transition: .3s;
  }

  .checkmark:after {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    content: "";
    width: 18px;
    height: 18px;
    background: transparent;
    border-radius: 50px;
    border: 1px solid #828BB2;
    transition: .3s;
    transform: scale(1);
  }
}

