// slide 

// switch_toggleer 
.switch_toggle {
  display: inline-block;
  height: 20px;
  position: relative;
  // width: 40px;
  width: 36px;
  margin-bottom: 0;

  input {
    opacity: 0;
  }

  .slider:before {
    bottom: 0px;
    content: "";
    // height: 20px;
    height: 16px;
    left: 0px;
    left: 3px;
    position: absolute;
    -webkit-transition: .4s;
    transition: .4s;
    // width: 20px;
    width: 16px;
    background: #707DB7;
    background: #fff;
    // box-shadow: 0px 5px 10px rgba(112, 125, 183, 0.25)
    top: 2px;
  }

  input:checked+.slider {
    background: #ECEEF4;
  }

  input:checked+.slider:before {
    // transform: translateX(20px);
    transform: translateX(14px);
    @extend %gradient_1;
    // box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25)
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .switch_toggle input {
    display: none;
  }

  .slider {
    background-color: #ECEEF4;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: .4s;
  }
}


// pusle 

.pulse {
  position: relative
}

.pulse .pulse-ring {
  display: block;
  border-radius: 40px;
  height: 40px;
  width: 40px;
  position: absolute;
  -webkit-animation: animation-pulse 3.5s ease-out;
  animation: animation-pulse 3.5s ease-out;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  opacity: 0;
  border-width: 3px;
  border-style: solid;
  border-color: #e4e6ef;

  &.notification_count_pulse {
    right: -18px;
    top: -14px;
  }
}

