.lms_supper_text {
    vertical-align: super !important;
}

.gj-picker-md {
    border: 1px solid #fef1f2;
    padding: 10px;
}

.gj-picker-md table tr td.today div {
    color: #fff;
    background: #fe1724;
    border-radius: 50%;
}

.gj-picker-md table tr td.gj-cursor-pointer div:hover {
    border-radius: 50%;
    background: #fe1724;
    color: #fff;

}

.gj-picker-md div[role="navigator"] div:first-child,
.gj-picker-md div[role="navigator"] div:last-child {
    max-width: 42px;
    background: #fef1f2;
    color: #000;
    border-radius: 50%;
    font-size: 14px;
    line-height: 44px;
    height: 42px;
    font-weight: 900;

    i {
        font-weight: 900;
        font-size: 14px;
    }
}

// dropdown 
.dropdown-menu {
    &.show {
        .dropdown-toggle.lms_drop_1::after {
            color: #fff !important;
        }
    }

    &.option_width {
        min-width: 102px;
    }
}

.dropdown.show .dropdown-toggle.lms_drop_1::after {
    color: #fff;
}


