        color: $white;
        padding: 0;
        height: 30px;
        line-height: 30px;
        border: 0;
        display: inline-block;

        &:hover,
        &:focus {
            color: lighten(#555, 20%);
            border-color: lighten(#555, 20%);
        }
    }

    /* input file style */

    .input-file,
    .input-file2 {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
