}

.lms_checkbox_1 {
    display: inline-block;
    height: 30px;
    position: relative;
    width: 50px;
    margin-bottom: 0;
    margin-right: 15px;

    input {
        display: none !important;
    }

    .slider-check {
        background-color: #7e7172;
        bottom: 0;
        cursor: pointer;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: .4s;

        &.round {
            border-radius: 34px;
        }

        &::before {
            background-color: #222222;
            bottom: 5px;
            content: "";
            height: 20px;
            left: 4px;
            position: absolute;
            transition: .4s;
            width: 20px;
        }

        &.round:before {
            border-radius: 50%;
        }
    }

    input:checked+.slider-check {
        background-color: #fef1f2;
        color: #fff;
        content: "oui";
    }

    input:checked+.slider-check:before {
        transform: translateX(20px);
        background: #fe1724;
    }
}



