.datepicker thead tr:first-child th {

    &.prev,
    &.next {
        position: relative;
        color: transparent;

        &::before {
            position: absolute;
            content: "\e629";
            font-family: 'themify';
            font-size: 15px;
            left: -2px;
            top: 0;
            color: #415094;
            width: 100%;
            height: 100%;
            text-align: center;
            line-height: 35px;
            font-size: 13px;
        }

        &:hover::before {
            color: #fff;
        }
    }

    &.next {
        &::before {
            position: absolute;
            content: "\e628";
            font-family: 'themify';
            font-size: 15px;
            left: -2px;
            top: 0;
            color: #415094;
            width: 100%;
            height: 100%;
            text-align: center;
            line-height: 35px;
            font-size: 13px;
        }
    }
}

.datepicker.dropdown-menu {
    padding: 20px;

    .datepicker-months,
    .datepicker-years {
        thead {
            tr {
                display: grid;
                grid-template-columns: 35px 180px 35px;

                th {
                    padding: 0;
                    line-height: 35px;

                    &.prev {
                        height: 35px;
                        width: 35px;
                        border-radius: 50%;
                    }

                    &.datepicker-switch {
                        height: 35px;
                        width: 160px;
                        margin: auto;
                    }

                    &.next {
                        height: 35px;
                        width: 35px;
                        border-radius: 50%;
                    }
                }
            }
        }

        tbody {
            tr {

                td {
                    width: 100%;
                    height: 100%;
                    display: grid;
                    padding: 0;
                    grid-template-columns: repeat(4, 1fr);
                    margin-top: 10px;

                    span.month,
                    span.year {
                        width: 45px;
                        height: 45px;
                        line-height: 45px;
                        text-align: center;
                        padding: 0 !important;
                        font-size: 12px;
                        border-radius: 50%;
                    }
                }
            }
        }
    }

    .datepicker-days {
        table {
            thead {
                >tr {
                    display: grid;
                    grid-template-columns: 35px 180px 35px;

                    th {
                        padding: 0;
                        line-height: 35px;

                        &.prev {
                            height: 35px;
                            width: 35px;
                            border-radius: 50%;
                        }

                        &.datepicker-switch {
                            height: 35px;
                            width: 160px;
                            margin: auto;
                        }

                        &.next {
                            height: 35px;
                            width: 35px;
                            border-radius: 50%;
                        }
                    }
                }

                tr:last-child {
                    display: grid;
                    margin-top: 10px;
                    grid-template-columns: repeat(7, 1fr);

                    th {
                        width: 35px;
                        height: 35px;
                        text-align: center;
                        line-height: 35px;
                    }
                }
            }

            tbody {
                tr {
                    display: grid;
                    grid-template-columns: repeat(7, 1fr);

                    td {
                        &.day {
                            width: 35px;
                            height: 35px;
                            line-height: 35px;
                            text-align: center;
                            padding: 0 !important;
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}

.datepicker.dropdown-menu th,
.datepicker.dropdown-menu td {
    color: #415094;
}

// single-summery fix #####
.white-box {
    &.single-summery {
        background-size: cover !important;
        background-position: right center;

        &:hover {
            background-size: cover !important;
            background-position: right center;
        }

        &::before {
            @media #{'only screen and (min-width: 992px) and (max-width: 1440px)'} {
                min-height: 136px;
                width: 90%;
            }
        }

        &::after {
            @media #{'only screen and (min-width: 992px) and (max-width: 1440px)'} {
                min-height: 136px;
                width: 80%;
            }
        }

        &:hover {
            &::before {
                @media #{'only screen and (min-width: 1440px) and (max-width: 1900px)'} {
                    border-radius: 10px;
                    background-size: 90% 100%;
                }

                @media #{'only screen and (min-width: 992px) and (max-width: 1440px)'} {
                    border-radius: 10px;
                    background-size: cover;
                    width: 90%;
                    left: 5%;
                }

                @media #{'only screen and (min-width: 320px) and (max-width: 580px)'} {
                    border-radius: 10px;
                    background-size: 90% 100%;
                }
            }

            &::after {
                @media #{'only screen and (min-width: 1440px) and (max-width: 1900px)'} {
                    border-radius: 10px;
                    background-size: 80% 100%;
                }

                @media #{'only screen and (min-width: 992px) and (max-width: 1440px)'} {
                    border-radius: 10px;
                    background-size: cover;
                    width: 80%;
                    left: 10%;
                }

                @media #{'only screen and (min-width: 320px) and (max-width: 580px)'} {
                    border-radius: 10px;
                    background-size: 80% 100%;
                }
            }
        }
    }
}

