.custom_nav {
    display: block !important;

    li {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        a {
            font-size: 14px;
            font-weight: 400;
            color: $base_color;
            padding: 0;

            &:hover {
                color: #7C32FF;
            }

            &.active {
                color: #7C32FF;
            }
        }
    }
}

