.General_system_wrap_area {
    display: grid;
    grid-template-columns: 4fr 8fr;
    grid-gap: 30px;

    @media #{$small_mobile} {
        grid-template-columns: repeat(1, 1fr);
    }

    @media #{$large_mobile} {
        grid-template-columns: repeat(1, 1fr);
    }

    .single_system_wrap {
        .single_system_wrap_inner {
            background: #F5F7FB;
            border-radius: 10px;
            padding: 25px 30px;
            margin-bottom: 30px;

            .logo {
                text-align: left;

                span {
                    font-size: 16px;
                    font-weight: 500;
                    color: $base_color;

                }
            }

            .logo_img {
                margin-top: 50px;
                margin-bottom: 30px;

                img {}
            }

            .update_logo_btn button {
                position: relative;
                overflow: hidden;
                font-size: 12px;
                margin-bottom: 13px;

                input {
                    position: absolute;
                    width: 100%;
                    left: 0;
                    top: 0;
                    opacity: 0;
                    height: 100%;
                    cursor: pointer;
                }
            }

            .remove_logo {
                font-size: 12px;
                color: #828BB2;
                font-weight: 500;
                text-transform: uppercase;
            }
        }
    }
}

