.menu_popUp_list_wrapper {
    background: #FFFFFF;
    box-shadow: 0px 10px 15px rgba(6, 0, 8, 0.22);
    border-radius: 10px;
    position: absolute;
    right: 0;
    width: 945px;
    top: 100%;
    // padding: 30px 37px 35px 50px;
    // padding: 30px 0;
    // display: grid;
    // grid-template-columns: repeat(4, 25%);
    display: flex;
    padding-left: 50px;
    padding-top: 37px;
    padding-bottom: 35px;
    display: flex;
    transform: translateY(30px) translateX(-18px);
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    background-image: url(../img/popup_menu_bg.png);
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
    padding-bottom: 71px;

    @media #{$small_mobile} {
        width: 94%;
        flex-wrap: wrap;
        height: 400px;
        overflow: auto;
        display: block;
    }

    @media #{$large_mobile} {
        width: 94%;
        flex-wrap: wrap;
        height: 400px;
        overflow: auto;
        display: block;

    }

    @media #{$tab} {
        width: 94%;
        height: 400px;
        overflow: auto;
        padding-bottom: 30px;

        padding-left: 30px;
    }

    @media #{$tablet_device} {
        flex-wrap: wrap;
    }

    @media #{$mid_device} {
        width: 800px;
        margin-right: -100px;
    }

    &.active {
        opacity: 1;
        visibility: visible;
        transform: translateY(20px) translateX(-18px);
    }

    &::before {
        position: absolute;
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        top: -12px;
        right: 12px;
        border-width: 0 8px 12px 8px;
        border-color: transparent transparent #fff transparent;

        @media #{$mid_device} {
            right: 112px;
        }
    }

    .popUp_single_wrap {
        width: 25%;
        border-right: 1px solid rgba(130, 139, 178, 0.15);
        margin-right: 45px;

        @media #{$small_mobile} {
            width: 100%;
            margin-right: 0;
        }

        @media #{$large_mobile} {
            width: 100%;
            margin-right: 0;
        }

        @media #{$tab} {
            margin-bottom: 30px;
        }

        @media #{$tablet_device} {
            width: 43%;
        }

        &:last-child {
            margin-right: 0;
            border: 0;
        }

        &:nth-child(2) {
            @media #{$tab} {
                margin-right: 0;
                border: 0;
            }
        }

        .popup_single_item {
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;
            }

            ul {
                display: block;

                li {
                    display: block !important;
                    margin: 0;
                    margin-bottom: 10px;

                    a {
                        font-size: 12px !important;
                        font-weight: 400 !important;
                        color: #415094 !important;
                        margin: 0;
                        transition: .3s;

                        i {
                            margin-right: 10px;
                            color: #415094;
                        }

                        &:hover {
                            color: #7C32FF !important;
                            text-decoration: underline;

                            i {
                                text-decoration: none !important;
                            }
                        }
                    }
                }
            }
        }

    }
}

// Menu_NOtification_Wrap 
.Menu_NOtification_Wrap {
    background: #FFFFFF;
    box-shadow: 0px 10px 15px rgba(6, 0, 8, 0.22);
    border-radius: 10px;
    position: absolute;
    right: 0;
    width: 350px;
    transform: translateY(30px) translateX(20px);
    opacity: 0;
    visibility: hidden;
    transition: .3s;

    @media #{$small_mobile} {
        width: 300px;
        transform: translateY(30px) translateX(55px);
    }

    &.active {
        opacity: 1;
        visibility: visible;
        transform: translateY(20px) translateX(20px);

        @media #{$small_mobile} {
            transform: translateY(16px) translateX(55px);
        }
    }

    .notification_Header {
        padding: 25px 20px;
        border-radius: 10px 10px 0px 0px;
        background: linear-gradient(90deg, #7c32ff 0%, #c738d8 100%);

        h4 {
            font-size: 20px;
            font-weight: 500;
            color: #fff;
            margin-bottom: 0;
        }
    }

    .Notification_body {
        padding: 25px 20px;
        height: 300px;
        overflow: auto;

        .single_notify {
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }

            .notify_thumb {
                margin-right: 15px;

                img {
                    width: 40px;
                    height: 40px;
                    border-radius: 5px;
                }
            }

            .notify_content {
                a {
                    h5 {
                        margin-bottom: 0;
                        font-size: 16px;
                        font-weight: 500;
                    }
                }

                p {
                    font-size: 13px;
                    font-weight: 400;
                    margin-bottom: 0;
                }
            }

        }
    }

    .nofity_footer {
        padding: 15px 20px;

        .submit_button {
            a {
                font-size: 12px;
                color: #fff !important;
            }
        }
    }
}