.primary_datepicker_input .primary_input_field.primary_input_field_auto:focus {
    border: 0 !important;
}

.primary_input_field {
    border: 1px solid #ECEEF4;
    font-size: 14px;
    color: #415094;
    padding-left: 20px;
    height: 46px;
    border-radius: 30px;
    width: 100%;
    padding-right: 15px;

    &.primary_input_field_auto {
        padding: 0;
        padding-top: 0px;
        padding-bottom: 0px;
        height: auto;
        border: 0;
        font-size: 13px;
        text-transform: none;

        &:focus {
            border: 0 !important;
        }

        &::placeholder {
            text-transform: none;
        }
    }

    &::-webkit-input-placeholder {
        color: #828bb2;
    }

    &::-moz-placeholder {
        color: #828bb2;
    }

    &:-ms-input-placeholder {
        color: #828bb2;
    }

    &::-ms-input-placeholder {
        color: #828bb2;
    }

    &::placeholder {
        color: #828bb2;
    }

    &.white_input {
        background: #fff;
    }
}

