.primary_input_label {
    font-size: 12px;
    text-transform: uppercase;
    color: $text_color_5;
    margin-bottom: 0;
    display: block;
    margin-bottom: 6px;
    font-weight: 400;
}

.green_input_label a {
    color: #54DA8A;
    white-space: nowrap;

    i {
        margin-left: 2px;
    }
}

.primary_select {
    width: 100%;
    border: 1px solid #ECEEF4;
    border-radius: 3px;
    height: 46px;
    line-height: 44px;
    font-size: 13px;
    color: #415094;
    padding: 0px 25px;
    padding-left: 20px;
    font-weight: 300;
    border-radius: 30px;

    &::placeholder {
        color: #415094;
        font-weight: 300;
        opacity: 1;
    }

    &:after {
        border-bottom: 0;
        border-right: 0;
        content: '';
        display: block;
        height: 10px;
        margin-top: 0;
        pointer-events: none;
        position: absolute;
        right: 14px;
        top: 10%;
        -webkit-transition: all 0.15s ease-in-out;
        transition: all 0.15s ease-in-out;
        width: auto;
        right: 25px;
        content: "\f0d7";
        border: 0;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: #828BB2;
        font-size: 14px;
        transform: translateY(-58%) rotate(0deg);

    }

    &.open::after {
        content: "\f0d8";
        transform: translateY(-58%) rotate(0deg);
        margin: 0;
    }

    &:active,
    &.open,
    &:focus,
    &:hover {
        border-color: #ECEEF4;
    }

    .list {
        width: 100%;
        background-color: #fff;
        overflow: auto !important;
        border-radius: 0px 0px 10px 10px;
        margin-top: 1px;
        z-index: 9999 !important;
        box-shadow: var(--box_shadow);

        .option {
            color: $base_color;

            &:hover {
                background-color: #f6f6f6;
                color: #7c32ff;
            }

            &.selected {
                font-weight: 400 !important;
            }
        }
    }
}

