.primary_datepicker_input {
    position: relative;

    button {
        position: absolute;
        color: #828BB2;
        font-size: 14px;
        font-weight: 400;
        right: 0;
        background: transparent;
        border: 0;
        cursor: pointer;
        z-index: 999;
        top: 50%;
        transform: translateY(-50%);
        right: 14px;

        i {
            top: 0;
            cursor: pointer;
            z-index: 9;
        }
    }

    .primary_input_field {
        padding-top: 0;
        padding-bottom: 0;

        &:focus {
            border: 1px solid #ECEEF4 !important;
        }
    }
}


