// only modal style ::::

.modal-content {
    border: 0;
    border-radius: 16px;

    // overflow: hidden;
    .modal-body {
        padding: 40px 30px;
        background-image: url(../img/popup_menu_bg.png);
        background-size: cover;
        background-position: bottom;
        border-radius: 0px 0px 15px 15px;
        background-repeat: no-repeat;
    }

    .modal-header {
        border-radius: 15px 15px 0px 0px;

        .close {
            background: #fff;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            flex: 32px 0 0;

            i {
                color: #7c32ff;
                font-size: 14px;
                line-height: 32px;
                text-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25)
            }
        }
    }
}

.modal_800px {
    @media #{'(min-width: 992px)'} {
        max-width: 811px;
    }

}

