.QA_section {
    .QA_table {
        .table {
            background: #FFFFFF;
            box-shadow: 0px 10px 15px rgba(235, 215, 241, 0.22);
            border-radius: 10px;
            // overflow: hidden;
            margin-bottom: 35px;
            padding-top: 20px;
            padding-bottom: 40px;
        }

        th,
        td {
            font-size: 13px;
            font-weight: 400;
            line-height: 1.9;
            color: $heading_color_1;
            padding: 16px 30px;
            vertical-align: middle;
            position: relative;
        }

        .question_content {
            color: $text_color;
            font-size: 13px;

            &:hover {
                color: $base_color;
            }
        }

        thead {

            th {
                white-space: nowrap;
                border-bottom: 1px solid rgba(130, 139, 178, .3) !important;
                padding-left: 45px !important;
            }
        }

        tbody {
            th {
                font-size: 14px;
                color: #415094;
                font-weight: 400 !important;
                // padding-left: 0;
            }

            th,
            td {
                color: $text_color;
                font-size: 14px;
                color: #828BB2;
                font-weight: 400;
                border-bottom: 1px solid rgba(130, 139, 178, .15);

                a {
                    font-size: 14px;
                    font-weight: 400;
                    color: #828BB2;

                    &.active_hover {
                        color: #7C32FF;
                    }

                    &:hover {
                        color: #7C32FF;
                    }
                }

                &.pending {
                    color: #E09079;
                    text-transform: uppercase;
                    font-size: 12px;
                    font-weight: 500;

                }

                &.priority_hight {
                    font-size: 14px;
                    color: #D7598F;
                    font-weight: 400;
                }
            }

            th {
                // white-space: nowrap;
            }

            tr {
                @include transition(.3s);

                // &:first-child{
                //     th, td{
                //         border: 0px solid transparent;
                //     }
                // }
                &:hover {
                    background: #fff;
                }
            }
        }

        .view_btn {
            color: $base_color;

            &:hover {
                text-decoration: underline;
            }
        }

        .table {
            margin-bottom: 0 !important;

            thead th {
                border-bottom: 0 solid transparent;
                background-color: $white;
                padding: 17px 30px;
                line-height: 16px;
                border: 0px solid transparent;
                font-size: 12px;
                font-weight: 400;
                color: $base_color;
                white-space: nowrap;
                text-transform: uppercase;
            }
        }
    }
}

