.check_box_table {
    .QA_table {
        .table {
            thead {
                th {
                    &:nth-child(2) {
                        padding-left: 0px;
                    }

                    &:first-child {
                        padding-left: 35px;
                        padding-right: 24px;
                    }
                }
            }

            tbody {
                th {
                    &:nth-child(2) {
                        padding-left: 0px;
                    }

                    &:first-child {
                        padding-left: 35px;
                        padding-right: 24px;
                    }
                }

                td {
                    &:nth-child(2) {
                        padding-left: 0px;
                    }

                    &:first-child {
                        padding-left: 35px;
                        padding-right: 24px;
                    }
                }
            }
        }
    }
}

